.m-breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    position: relative;
    color: #ffffff;
    margin-bottom: 0.9375em;
    &:not(:last-child) {
      margin-right: 1.25em;
      &:after {
        content: '';
        position: absolute;
        top: 0.25em;
        right: -0.75em;
        width: 0.0625em;
        height: 1em;
        background: #ffffff;
      }
    }
  }
}




.gallery2 {
  position: relative;
  z-index: 2;
  margin-top: -9.375em;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4.5em / 3));
    gap: 2.25em;
  }
  &-card {
    &__img {
      position: relative;
      padding-bottom: 100%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
    &__desc {
      margin-top: 1.25em;
    }
    &__title {
      font-weight: 600;
    }
    &__date {
      margin-top: 0.9375em;
      p {
        color: #689dc5;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .gallery2 {
    padding-top: 1.25em;
    margin-top: 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .gallery2 {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}