.gallery3 {
  position: relative;
  z-index: 2;
  margin-top: -9.375em;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4.5em / 3));
    gap: 2.25em;
  }
  &-card {
    position: relative;
    padding-bottom: 65%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 0.3125em;
    }
  }
  &-card--big {
   @media screen and (min-width: 577px) {
     grid-column: 1/4;
     padding-bottom: 46%;
   }
  }
}

@media screen and (max-width: 992px) {
  .gallery3 {
    padding-top: 1.25em;
    margin-top: 0;
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .gallery3 {
    &__cards {
      grid-template-columns: 100%;
    }
    &-card {
      &:nth-child(7) ~ .gallery3-card {
        display: none;
      }
    }
  }
}