.tour-reviews{
  margin-top: 3.75em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__items{
    margin-top: 1.625em;
    @include grid100gap(1.25em);
  }
  &__btn{
    margin-top: 1.25em;
  }
  &:not(._show-all){
    .review-card:nth-child(2)~.review-card{
      display: none;
    }
  }
  &._show-all{
    .tour-reviews__btn{
      display: none;
    }
  }
}

.review-card2{
  padding: 2em 2em 2.75em;
}

.articles{
  margin-top: 3.5em;
  &__items{
    margin-top: 2em;
    row-gap: 2.5em;
  }
}

.article-card{
  display: block;
  &__img{
    border-radius: .5em;
    margin-bottom: 1.25em;
    @include imgCover(65.8%);
  }
  .text22{
    line-height: 118%;
  }
}

.tours-collection{
  padding: 6.25em 0 0;
  background-color: #f7f7f7;
  clip-path: polygon( 0% 0%,5.556% 0.604%,5.556% 0.604%,7.383% 0.814%,9.511% 1.054%,11.906% 1.327%,14.533% 1.633%,17.361% 1.974%,20.356% 2.351%,23.483% 2.766%,26.711% 3.219%,30.006% 3.714%,33.333% 4.249%,33.333% 4.249%,36.667% 4.805%,40% 5.387%,43.333% 5.981%,46.667% 6.57%,50% 7.139%,53.333% 7.671%,56.667% 8.15%,60% 8.561%,63.333% 8.887%,66.667% 9.114%,66.667% 9.114%,69.994% 9.236%,73.289% 9.268%,76.517% 9.224%,79.644% 9.118%,82.639% 8.961%,85.467% 8.768%,88.094% 8.552%,90.489% 8.326%,92.617% 8.102%,94.444% 7.895%,100% 7.291%,100% 100%,0% 100%,0% 0% );
  position: relative;
  z-index: 3;
  overflow: hidden;
  margin-top: 5em;
}

.order{
  position: sticky;
  top: 5em;
  padding: 1.875em 2em 2em;
  background-color: #f7f7f7;
  border-radius: .5em;
  &__top{
    @include grid100gap(.5em);
  }
  &__fields{
    @include grid100gap(1.25em);
    margin-top: 2em;
    input{
      border: none;
      &::placeholder{
        color: $black;
      }
    }
  }
  &__calc{
    padding: 0 1em;
    background-color: #fff;
    border-radius: .25em;
    height: 2.75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__count{
    margin-top: .75em;
    color: $red;
  }
  &__additional{
    margin-top: 2em;
    &-items{
      margin-top: 1.25em;
      @include grid100gap(1em);
    }
  }
  &__nav{
    margin-top: 2em;
    @include grid100gap(1.25em);
  }
  &__whatsapp{
    padding: 0 1em;
    border-radius: 1.375em;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
}

.cartcalc{
  width: 5em;
  display: grid;
  grid-template-columns: 1em calc(100% - 2em) 1em;
  align-items: center;
  button{
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
    &._disabled{
      opacity: .4;
      pointer-events: none;
    }
  }
  input{
    text-align: center;
    font-size: 1em;
    border: none;
    width: 100%;
    height: 100%;
    background-color: transparent;
  }
}

.page-top2{
  padding: 8.75em 0 6.875em;
  background-position: center;
  position: relative;
  z-index: 2;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    clip-path: polygon( 100% 0%,0% 0%,0% 75.51%,0% 80.612%,0% 83.673%,5.556% 83.673%,5.556% 83.673%,7.383% 83.678%,9.511% 83.706%,11.906% 83.784%,14.533% 83.935%,17.361% 84.184%,20.356% 84.555%,23.483% 85.073%,26.711% 85.763%,30.006% 86.649%,33.333% 87.755%,33.333% 87.755%,34.444% 88.176%,35.556% 88.622%,36.667% 89.09%,37.778% 89.576%,38.889% 90.079%,40% 90.596%,41.111% 91.123%,42.222% 91.659%,43.333% 92.2%,44.444% 92.744%,44.444% 92.744%,46.667% 93.829%,48.889% 94.892%,51.111% 95.911%,53.333% 96.865%,55.556% 97.732%,57.778% 98.491%,60% 99.118%,62.222% 99.594%,64.444% 99.895%,66.667% 100%,66.667% 100%,69.994% 99.767%,73.289% 99.118%,76.517% 98.127%,79.644% 96.865%,82.639% 95.408%,85.467% 93.829%,88.094% 92.2%,90.489% 90.596%,92.617% 89.09%,94.444% 87.755%,100% 83.673%,100% 80.612%,100% 75.51%,100% 0% );
    background-image: url(../img/page-top-bg.jpg);
    background-size: cover;
    z-index: -1;
  }
  &__title{
    color: #fff;
  }
}

.filter{
  margin-top: 2.75em;
  display: flex;
  align-items: center;
  &__show-btn,
  &__apply,
  &__top{
    display: none;
  }
  &__items{
    display: flex;
  }
  .form-select{
    padding-left: 2.875em;
    height: 3em;
    border-radius: .5em;
  }
  &__nav{
    margin-left: .75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__clear{
    color: #fff;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
    span{
      font-size: .875em;
      line-height: 130%;
    }
  }
}

.filter-item{
  margin-left: .625em;
  position: relative;
  &:first-child{
    margin-left: 0;
  }
  &._active{
    .filter-item__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__title{
    display: none;
  }
  &__toggle{
    padding: 0 1em;
    display: flex;
    align-items: center;
    background-color: #fff;
    border-radius: .5em;
    height: 3em;
    cursor: pointer;
    &:after{
      content: '';
      flex-shrink: 0;
      margin-left: auto;
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%2376767a' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
      @include bgImgSize(100%);
      width: .875em;
      height: .875em;
    }
  }
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .5em;
  }
  &__current{
    margin-right: 1.5em;
  }
  &__dropdown{
    box-shadow: 0 13px 24px 0 rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .25em;
    min-width: 100%;
    z-index: 2;
    background-color: #fff;
    padding: .5em .75em;
    border-radius: .25em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__checks{
    @include grid100gap(.75em);
    white-space: nowrap;
  }
  &__range{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.125em;
    min-width: 20em;
    padding: 1em 0;
    input{
      border-color: #f6f7f8;
      background-color: #f6f7f8;
      height: 3em;
      border-radius: .5em;
      padding-left: 2.25em;
    }
    &-title{
      position: absolute;
      left: .8125em;
      top: 50%;
      transform: translateY(-50%);
      font-size: 1em;
    }
  }
}

.tour-catalog{
  margin-top: 2.5em;
  .popular-tours__items{
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .tour-reviews{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      margin-top: 1em;
    }
    &__items{
      margin-top: 1.875em;
    }
  }

  .review-card2{
    padding: 1.375em 1.25em 1.875em;
    .review-card__info{
      margin-top: 1.25em;
      .text22{
        font-size: 1.125em;
      }
    }
  }

  .articles{
    margin-top: 3.75em;
    &__items{
      margin-top: 1.375em;
      display: flex;
      gap: unset;
      width: calc(100% + 2.25em);
      margin-left: -1.125em;
      padding: 0 1.125em;
      overflow-x: auto;
    }
    .article-card{
      width: 20em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }

  .article-card{
    &__img{
      margin-bottom: 1em;
    }
    .text22{
      font-size: 1.125em;
      line-height: 133%;
    }
  }

  .page-top2{
    padding: 6.625em 0 2.25em;
  }

  ._show-filter{
    .header{
      z-index: -1;
    }
    .filter{
      transform: translateX(0);
    }
  }

  .filter{
    transition: all .4s;
    transform: translateX(-120%);
    padding: 1.5em 1.25em;
    margin: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    overflow-y: auto;
    z-index: 30;
    background-color: #fff;
    display: block;
    &__top{
      margin-bottom: 1.875em;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__close{
      width: 1.5625em;
      @include center;
      img{
        width: 100%;
        display: block;
      }
    }
    &__items{
      @include grid100gap(1.25em);
    }
    &__nav{
      margin: 2.625em 0 0;
    }
    &__apply,
    &__clear{
      color: $black;
    }
    &__apply{
      display: flex;
    }
    &__show-btn{
      display: flex;
      align-items: center;
      color: #fff;
      margin-top: 1.25em;
      span{
        font-size: 1em;
        line-height: 125%;
      }
    }
  }

  .filter-item{
    margin: 0;
    &:not(:last-child){
      padding-bottom: 2em;
      border-bottom: solid 1px #cddbe3;
    }
    &__toggle{
      display: none;
    }
    &__title{
      margin-bottom: 1em;
      display: block;
      font-size: 1em;
      line-height: 125%;
    }
    &__dropdown{
      opacity: 1;
      pointer-events: unset;
      position: static;
      padding: 0;
      box-shadow: unset;
    }
    &__range{
      padding: 0;
    }
    &._sort{
      .m-check {
        label{
          padding: 0;
          &:before{
            display: none;
          }
        }
        input:checked~label{
          color: $secondary;
        }
      }
    }
  }

  .tour-catalog{
    margin-top: 2em;
  }
}


@media screen and (max-width: 576px){
  .tours-collection{
    margin-top: 4em;
    clip-path: polygon( 0% 0%,5.556% 0.185%,5.556% 0.185%,7.383% 0.249%,9.511% 0.322%,11.906% 0.405%,14.533% 0.499%,17.361% 0.603%,20.356% 0.719%,23.483% 0.845%,26.711% 0.984%,30.006% 1.135%,33.333% 1.299%,33.333% 1.299%,36.667% 1.469%,40% 1.647%,43.333% 1.828%,46.667% 2.008%,50% 2.182%,53.333% 2.344%,56.667% 2.491%,60% 2.617%,63.333% 2.716%,66.667% 2.786%,66.667% 2.786%,69.994% 2.823%,73.289% 2.833%,76.517% 2.819%,79.644% 2.787%,82.639% 2.739%,85.467% 2.68%,88.094% 2.614%,90.489% 2.545%,92.617% 2.476%,94.444% 2.413%,100% 2.228%,100% 100%,0% 100%,0% 0% );
    padding: 3.375em 0 1em;
  }
}
