.about {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    padding: 34.375em 0 3.125em;
    position: relative;
    z-index: 2;
  }
  &__title {
    color: #fff;
  }
}
.info {
  padding-bottom: 2.5em;
  &__top {
    background: #005593;
    padding: 3.125em 0;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 6.5625em / 4));
    gap: 2.1875em;
  }
  &__item {
    &-numbers {
      font-size: 3.25em;
      line-height: 105%;
      font-weight: 800;
      color: #7accf2;
    }
    &-text {
      font-size: 1.5em;
      line-height: 130%;
      color: #ffffff;
      margin-top: 0.9375em;
    }
  }
  &__bottom {
    margin-top: 3.125em;
  }
  &__desc {
    display: grid;
    grid-template-columns: 100%;
    gap: 2em;
  }
  &__subtitle {
    font-size: 1.5em;
    line-height: 130%;
    color: #000000;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
}
.advantages {
  padding: 2.5em 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4.375em / 3));
    gap: 2.1875em;
    margin-top: 2.1875em;
  }
  &__item {
    background: #99caf5;
    padding: 2.8125em 1.5625em;
    &-title {
      font-size: 2.25em;
      line-height: 120%;
      font-weight: 700;
      color: #061058;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.625em;
      margin-top: 0.9375em;
      p {
        color: #061058;
      }
    }
  }
}
.adventures {
  padding: 2.5em 0;
  &__desc {
    max-width: 40em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5em;
    margin-left: 20.625em;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
  }
}
.team {
  padding: 2.5em 0;
  &__top {
    max-width: 54.375em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5625em;
  }
  &-slider {
    margin-top: 2.1875em;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4)));
      gap: 2.25em 0;
      transform: unset !important;
    }
    .swiper-slide {
      width: 100%;
    }
  }
}
.team-card {
  &__img {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    margin-top: 1.125em;
  }
  &__name {
    font-weight: 600;
  }
  &__post {
    margin-top: 0.5em;
  }
}
.team-gallery {
  padding: 2.5em 0;
  overflow: hidden;
  .slider__wrap {
    overflow: unset;
  }
  .swiper-slide {
    width: 26.75em;
  }
  &__card {
    display: block;
    position: relative;
    padding-bottom: 80%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
.quote {
  padding: 2.5em 0;
  &__inner {
    max-width: 67.5em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
  }
  &__left {
    width: 25.625em;
    margin-right: 2.25em;
  }
  &__right {
    width: calc(100% - 27.875em);
  }
  &__img {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.875em;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
  }
  &__subtitle {
    font-weight: 600;
  }
}
.certificates {
  padding: 2.5em 0;
  &-slider {
    margin-top: 2.25em;
  }
  .slider__wrap {
    overflow: unset;
  }
  .swiper-slide {
    width: 13.75em;
  }
}
.certificate-card {
  display: block;
  position: relative;
  padding-bottom: 145%;
  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

@media screen and (max-width: 992px) {
  .about {
    &__inner {
      padding: 25em 0 2.5em;
    }
  }
  .info {
    padding-bottom: 1.25em;
    &__top {
      padding: 2.5em 0;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1em / 2));
      gap: 1.25em 1em;
    }
    &__item {
      &-text {
        font-size: 1.25em;
      }
    }
    &__subtitle {
      font-size: 1.375em;
    }
  }
  .advantages {
    padding: 1.25em 0;
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
      margin-top: 1.25em;
    }
    &__item {
      padding: 1.5625em 1.125em;
      &-title {
        font-size: 1.75em;
      }
    }
  }
  .adventures {
    padding: 1.25em 0;
    &__desc {
      max-width: 100%;
      margin-left: 0;
    }
  }
  .team {
    padding: 1.25em 0;
    overflow: hidden;
    .swiper-slide {
      width: 18.75em;
    }
  }
  .team-gallery {
    background: #ebf2f6;
    padding: 2em 0;
    margin: 1.25em 0;
    .swiper-slide {
      width: 14.5em;
    }
    &__card {
      padding-bottom: 140%;
    }
  }
  .quote {
    padding: 1.25em 0;
    &__inner {
      display: block;
    }
    &__left {
      max-width: 20em;
      width: 100%;
      margin: 0 0 1.25em 0;
    }
    &__right {
      width: 100%;
    }
  }
  .certificates {
    padding: 1.25em 0;
    overflow: hidden;
    &-slider {
      margin-top: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .advantages {
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      &-title {

      }
    }
  }
}
