.place{
  color: #fff;
  position: relative;
  overflow: hidden;
  .swiper{
    &-container{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    &-slide{
      background-size: cover;
      background-position: center;
    }
    &-nav{
      display: flex;
      align-items: center;
      z-index: 2;
      &__fraction{
        margin: 0 1.25em;
      }
    }
    &-button{
      position: static;
      transform: unset;
      margin: 0;
    }
    &-pagination{
      position: static;
      width: unset;
      font-size: 1.125em;
    }
  }
  .container{
    min-height: 41.25em;
    display: flex;
    flex-direction: column;
  }
  &__inner{
    margin-top: auto;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 6em 0 3.5em;
  }
  &__hotel{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
  }
}

.excursion{
  margin-top: 3.75em;
  &__items{
    margin-top: 2.25em;
    @include grid100gap(1.5em);
  }
}

.excursion-card{
  display: grid;
  grid-template-columns: 18.625em calc(100% - 18.625em - 2.25em);
  gap: 2.25em;
  &__left{
    position: relative;
  }
  .favourite-btn{
    position: absolute;
    top: .625em;
    right: .625em;
    z-index: 2;
  }
  &__img{
    display: block;
    @include imgCover(73.9%)
  }
  &__right{
    padding-top: .625em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .tour-card__info{
    margin-top: 1em;
  }
  .tour-card__price{
    margin: 1.5em 0 0;
  }
}

.hotel-info{
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }
  .living__stars{
    margin: 0 2.5em 0 0;
  }
  .living__desc{
    margin: 0 0 0 2.5em;
  }
}


@media screen and (max-width: 992px){
  .place{
    .swiper{
      &-nav{
        order: -1;
        margin-bottom: 1em;
        &__fraction{
          margin: 0;
          padding: 0 .875em;
          background-color: rgba(0, 0, 0, 0.4);
          border-radius: 1.5em;
          height: 2.75em;
          display: flex;
          align-items: center;
        }
      }
      &-button{
        display: none;
      }
    }
    .container{
      min-height: $vh;
    }
    &__inner {
      flex-direction: column;
      padding-top: 10em;
      padding-bottom: 2em;
      align-items: flex-start;
    }
    &__hotel{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .excursion{
    &__items{
      margin-top: 2em;
    }
  }

  .hotel-info{
    &__bottom{
      margin-top: 3em;
      flex-wrap: wrap;
    }
    .living__desc{
      width: 100%;
      margin: 1.5em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .excursion{
    &__items{
      gap: 2.25em;
    }
  }

  .excursion-card{
    grid-template-columns: 100%;
    gap: 1em;
    &__right{
      padding: 0;
    }
    .tour-card__price{
      margin-top: 1.25em;
    }
  }
}
