.video-main {
  position: relative;
  z-index: 2;
  margin-top: -9.375em;
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2.25em / 2));
    gap: 2.25em;
  }
  &__card {
    &-top {
      position: relative;
      padding-bottom: 65%;
    }
    .video__img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 100%;
    }
    &-desc {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.5em;
      margin-top: 1.25em;
    }
    &-title {
      font-weight: 600;
    }
    &-date {
      color: #689dc5;
    }
  }
}

@media screen and (max-width: 992px) {
  .video-main {
    padding-top: 1.25em;
    margin-top: 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
    &__card {
      &-title {
        font-size: 1.125em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .video-main {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}