.tour{
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    min-height: 41.25em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 6em 0 3.75em;
    position: relative;
    z-index: 2;
  }
  &__text{
    @include grid100gap(1.25em);
  }
  &__date{
    color: #7accf2;
  }
  &__nav{
    margin-left: 1em;
    flex-shrink: 0;
    padding-bottom: 3em;
  }
}

.tour-nav{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  transition: all .4s;
  box-shadow: 0 13px 24px 0 rgba(0, 0, 0, 0.1);
  &:not(._fixed){
    transform: translateY(-120%);
  }
  &__inner{
    padding: 1.125em 0;
    display: flex;
    align-items: center;
  }
  &__link{
    font-weight: 600;
    @include lastM(0 3.75em 0 0);
    span{
      font-size: 1.125em;
      line-height: 133%;
    }
    &._active{
      color: $secondary;
    }
  }
}

.tour-detail{
  padding: 2.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 25.625em - 2.25em) 25.625em;
    gap: 2.25em;
  }
}

.tour-props{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.125em));
    gap: 1.5em 2.25em;
  }
  &__item{
    background-color: #f7f7f7;
    border-radius: .5em;
    padding: 1.1875em 1.375em;
  }
  &__text{
    margin-top: .5em;
    .text18{
      line-height: 133%;
    }
  }
  &__btn{
    margin-top: 1em;
  }
  &:not(._show-all){
    .tour-props__item:nth-child(4)~.tour-props__item{
      display: none;
    }
  }
  &._show-all{
    .tour-info__btn{
      display: none;
    }
  }
}

.more-btn{
  @include center;
  height: 2em;
  background-color: #f7f7f7;
  border-radius: .5em;
  width: 100%;
  &:hover{
    background-color: darken(#f7f7f7,10%);
  }
  img{
    width: .875em;
    display: block;
  }
}

.tour-desc{
  margin-top: 2.5em;
  &:not(._show-all){
    .text *:nth-child(3)~*{
      display: none;
    }
  }
  &__btn{
    margin-top: 1.5em;
  }
  &._show-all{
    .tour-desc__btn{
      display: none;
    }
  }
}

.tour-program{
  margin-top: 3.75em;
  &__items{
    margin-top: 2em;
  }
}

.program-card{
  border-top: solid 1px #cddbe3;
  &:last-child{
    border-bottom: solid 1px #cddbe3;
  }
  &.slideToggle-block--active{
    .program-card__btn{
      img{
        transform: rotate(-180deg);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 1.0625em 0;
  }
  &__btn{
    padding: 0;
    width: 2.75em;
    height: 2.75em;
    border-radius: 50%;
    @include center;
    flex-shrink: 0;
    margin-left: 1em;
    img{
      width: .875em;
      transition: all .3s;
    }
  }
  .program-card__wrap{
    padding-top: .1875em;
    padding-bottom: 1.875em;
  }
  &__text{
    margin-top: 2em;
    &:nth-child(1){
      margin-top: 0;
    }
    p{
      @include lastM(0 0 .75em);
    }
  }
}

.gallery{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
  gap: .5em;
}

.gallery-item{
  display: block;
  position: relative;
  &__img{
    display: block;
    border-radius: .5em;
    @include imgCover(77.2%);
  }
  &__count{
    position: absolute;
    border-radius: .5em;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include center;
    z-index: 2;
    color: #fff;
    background-color: rgba(0, 71, 118, 0.6);
    font-weight: 600;
  }
}

.visit-places{
  margin-top: 2.5em;
  padding: 1.5em 1.25em 2.25em;
  background-color: #f7f7f7;
  border-radius: .5em;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .5625em));
    gap: .75em;
    margin-top: 1.5em;
  }
}

.place-card{
  position: relative;
  &__text{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: .4375em .9375em;
    color: #fff;
    z-index: 2;
  }
  &__img{
    display: block;
    border-radius: .5em;
    @include imgCover(75%);
    &:before{
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.7));
      z-index: 2;
    }
  }
}

.tour-video{
  margin-top: 3.75em;
  .video{
    margin-bottom: 1.25em;
  }
}

.video{
  display: block;
  position: relative;
  &__play{
    @include centerAbsolute;
    width: 3.75em;
    &:hover{
      opacity: .9;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: .5em;
  }
}

.tour-contain{
  margin-top: 3.75em;
  @include grid100gap(2.5em);
  &__group{
    display: grid;
    grid-template-columns: 12em calc(100% - 14.5em);
    gap: 2.5em;
    align-items: flex-start;
    &._type2{
      .tour-contain__list li:before{
        background-image: url(../img/close-red.svg);
      }
    }
  }
  &__list{
    @include grid100gap(.75em);
    li{
      position: relative;
      padding-left: 1.875em;
      &:before{
        content: '';
        position: absolute;
        left: 0;
        top: .25em;
        width: .9375em;
        height: .75em;
        background-image: url(../img/done-green.svg);
        @include bgImgSize(100%);
      }
      span{
        font-size: 1.125em;
        line-height: 133%;
      }
    }
  }
}

.tour-gallery{
  margin-top: 3.75em;
  .gallery{
    margin-top: 1.875em;
  }
  @media screen and (min-width: 577px){
    .gallery{
      display: grid;
      grid-template-columns: repeat(4,calc(25% - .9375em / 4));
      gap: .3125em;
      ._big{
        grid-column: span 2;
        grid-row: span 2;
      }
    }
  }
}

.should-know{
  margin-top: 3.75em;
}

.living{
  margin-top: 3.75em;
  &__wrap{
    margin-top: 2em;
    display: grid;
    grid-template-columns: 18.75em calc(100% - 21em);
    gap: 2.25em;
  }
  &__img{
    border-radius: .5em;
    @include imgCover(75.6%);
  }
  &__title{
    line-height: 118%;
  }
  &__stars{
    display: flex;
    align-items: center;
    margin-top: 1em;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
  &__desc{
    margin-top: 1.5em;
    a{
      color: #f8ca00;
      &:hover{
        color: $black;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .tour{
    &__inner{
      min-height: $vh;
      padding: 5em 0 1.75em;
    }
    &__text{
      gap: 1em;
      padding-bottom: .3125em;
    }
    &__nav{
      padding-bottom: 0;
      margin-left: .3125em;
    }
  }

  .tour-nav{
    .container{
      padding: 0;
    }
    &__inner{
      padding: 1.125em;
      overflow-x: auto;
    }
    &__link{
      @include lastM(0 1.5em 0 0);
      span{
        font-size: 1em;
        line-height: 150%;
      }
    }
  }

  .tour-detail{
    padding: 2em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .tour-props{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__btn{
      margin-top: 1.25em;
    }
  }

  .tour-video{
    margin-top: 3.125em;
    .video{
      margin-bottom: 1em;
    }
  }

  .video{
    &__play{
      width: 3em;
    }
  }

  .tour-contain{
    gap: 2.25em;
  }

  .living{
    &__wrap{
      margin-top: 1.125em;
      grid-template-columns: 14em calc(100% - 15em);
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .tour-props{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .program-card{
    width: calc(100% + 2.25em);
    margin-left: -1.125em;
    &__show{
      padding: 1.625em 1.25em;
    }
    .slideToggle-block__wrap{
      padding: 0 1.25em 2em;
    }
  }

  .gallery,
  .visit-places,
  .visit-places__items,{
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
  }

  .gallery,
  .visit-places__items{
    display: flex;
    gap: unset;
    overflow-x: auto;
  }

  .gallery{
    padding: 0 1.25em .25em;
  }

  .gallery-item{
    width: 17.5em;
    flex-shrink: 0;
    @include lastM(0 .5em 0 0);
  }

  .visit-places{
    padding: 1.5em 1.25em .5em;
    &__items{
      padding: 0 1.25em 1em;
    }
  }

  .place-card{
    width: 12.5em;
    margin-right: .75em;
    flex-shrink: 0;
    &__text{
      padding: 1em 1.5em;
    }
  }

  .tour-contain{
    .text26{
      font-size: 1.5em;
      line-height: 125%;
      br{
        display: none;
      }
    }
    &__group{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .tour-gallery {
    .gallery {
      margin-top: 1.125em;
    }
    .gallery-item{
      width: 18.75em;
      @include lastM(0 .625em 0 0);
    }
  }

  .living{
    &__wrap{
      grid-template-columns: 100%
    }
  }
}
