.page-top {
  position: relative;
  padding: 0;
  &__inner {
    position: relative;
    z-index: 2;
    padding: 9.375em 0 12.5em;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    color: #ffffff;
  }
  &__date {
    color: #ffffff;
    margin-left: 1.5em;
  }
}

.photo {
  position: relative;
  z-index: 2;
  margin-top: -9.375em;
  &__cards {
    max-width: 67.5em;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2.25em / 2));
    gap: 2.25em;
    margin: 0 auto;
  }
}

.photo-card {
  display: flex;
  flex-direction: column;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 3.75em 2.5em;
  &__title {
    color: #ffffff;
    margin-bottom: auto;
  }
  &__text {
    margin-top: 16.875em;
    p {
      color: #ffffff;
    }
  }
}

@media screen and (max-width: 992px) {
  .page-top {
    &__inner {
      padding: 6.25em 0;
    }
    &__date {
      display: none;
    }
  }

  .photo {
    margin-top: 0;
    padding-top: 1.25em;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
  }

  .photo-card {
    padding: 1.25em 1em;
    &__text {
      margin-top: 8.125em;
      p {
        font-size: 1em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .photo {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}
